import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { TypeOfApplication } from '@models';
import { ApiService, SweetAlertService } from '@services';
import { COMMON_FUNCTION, ENDPOINTS, FORM_TYPE, STATUSES } from '@shared';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-type-of-application',
  templateUrl: './type-of-application.component.html',
  styleUrls: ['./type-of-application.component.scss']
})
export class TypeOfApplicationComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<TypeOfApplication>;
  displayedColumns: string[] = ['description', 'clearingReleaseTime', 'decisionReleaseTime', 'submissionStartTime', 'submissionEndTime'];;
  statusList = [...STATUSES];

  typeOfApplication = new TypeOfApplication();
  typeOfApplicationList = [];

  formTypeList = [...FORM_TYPE];

  typeOfApplicationForm: FormGroup;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService
  ) { }

  ngOnInit(): void {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.applicationType).subscribe((res: any) => {
      console.log(res.responseData.data);
      this.typeOfApplicationList = res.responseData.data;
      this.dataSource = new MatTableDataSource<TypeOfApplication>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save(): void {
    this.apiService.save(ENDPOINTS.applicationType, this.typeOfApplication).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {

        this.resetFormValidator(),
          this.getList();
      }
    )
  }


  getTypeOfApplication(): void {
    this.apiService.findById(ENDPOINTS.applicationType, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.typeOfApplication = res.responseData.data;
      }
    });
  }


  update(): void {
    // this.typeOfApplication.modifiedBy = "J. Dela Cruz";
    this.apiService.update(ENDPOINTS.applicationType, this.typeOfApplication).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {

        this.resetFormValidator();
        this.getList();
      });
  }

  setFormValidator(): void {
    formType: new FormControl();
    this.typeOfApplication.status = COMMON_FUNCTION.ACTIVE;
    this.typeOfApplicationForm = this.formBuilder.group({
      // formType: ['', Validators.required],
      clearingReleaseTime: ['', [Validators.required]],
      decisionReleaseTime: ['', [Validators.required]],
      description: ['', [Validators.required]],
      sequenceNo: ['', [Validators.required]],
      status: ['', [Validators.required]],
      submissionEndTime: ['', [Validators.required]],
      submissionStartTime: ['', [Validators.required]],
    });
  }

  resetFormValidator(): void {
    this.typeOfApplication = new TypeOfApplication();
    this.typeOfApplication.status = COMMON_FUNCTION.ACTIVE;
    this.typeOfApplicationForm.markAsUntouched();
    this.typeOfApplicationForm.markAsPristine();

  }

  compareFormType(value: any, selected: any): boolean {
    if (value && selected) {
      return value.description === selected;
    }
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this type of application?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.applicationType, this.typeOfApplication.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Type of application successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }
}
